<template>
  <div class="Admin">
  		 <Layout>
            <Sider ref="side1" hide-trigger collapsible :collapsed-width="78" v-model="isCollapsed" v-elsize='{width:"200px",height:"0"}'  breakpoint="sm">
                <Menu active-name="1-1" theme="dark" width="auto" :class="menuitemClasses">
                    <router-link to='/'>
                        <MenuItem name="1-1">
                            <Icon type="ios-navigate"></Icon>
                            <span>状态</span>
                        </MenuItem>
                    </router-link>
                    <router-link to='/AdminDevice'>
                        <MenuItem name="1-2">
                            <Icon type="search"></Icon>
                            <span>设备效率分析</span>
                        </MenuItem>
                    </router-link>
                    <router-link to='/Achievements'>
                        <MenuItem name="1-3">
                           <Icon type="android-contacts"></Icon>
                            <span>人员绩效</span>
                        </MenuItem>
                    </router-link>
                    <router-link to='/Schedule'>
                        <MenuItem name="1-4">
                            <Icon type="ios-pulse-strong"></Icon>
                            <span>生产进度</span>
                        </MenuItem>
                    </router-link>
                </Menu>
            </Sider>
            <Layout>
                <Header :style="{padding: 0}" class="layout-header-bar">
                    <Icon @click.native="collapsedSider" :class="rotateIcon" :style="{margin: '20px 20px 0'}" type="navicon-round" size="24"></Icon>
                    <h2 class="title">设备状态</h2>
                </Header>
                <Content :style="{margin: '20px', background: '#fff', minHeight: '260px'}">
                

                 <router-view></router-view>
                </Content>
            </Layout>
        </Layout>
  </div>
</template>

<script>
export default {
  name: 'Admin',
   data () {
            return {
                isCollapsed: false
            }
        },
        computed: {
            rotateIcon () {
                return [
                    'menu-icon',
                    this.isCollapsed ? 'rotate-icon' : ''
                ];
            },
            menuitemClasses () {
                return [
                    'menu-item',
                    this.isCollapsed ? 'collapsed-menu' : ''
                ]
            }
        },
        methods: {
            collapsedSider () {
                this.$refs.side1.toggleCollapse();
            },
            menuitemClasses: function () {
                return [
                    'menu-item',
                    this.isCollapsed ? 'collapsed-menu' : ''
                ]
            }
        }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	 .layout{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }
    .layout-header-bar{
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
    .layout-logo-left{
        width: 90%;
        height: 30px;
        background: #5b6270;
        border-radius: 3px;
        margin: 15px auto;
    }
    .menu-icon{
        transition: all .3s;
    }
    .rotate-icon{
        transform: rotate(-90deg);
    }
    .menu-item span{
        display: inline-block;
        overflow: hidden;
        width: 100px;
        text-overflow: ellipsis;
        white-space: nowrap;
        vertical-align: bottom;
        transition: width .2s ease .2s;
    }
    .menu-item i{
        transform: translateX(0px);
        transition: font-size .2s ease, transform .2s ease;
        vertical-align: middle;
        font-size: 16px;
    }
    .collapsed-menu span{
        width: 0px;
        transition: width .2s ease;
    }
    .collapsed-menu i{
        transform: translateX(5px);
        transition: font-size .2s ease .2s, transform .2s ease .2s;
        vertical-align: middle;
        font-size: 22px;
    }
   
    .title{
    	display: inline-block;
    	text-align:center;
    	margin: 0 auto;
    }
</style>



