// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'  //路由功能 > 传统的A链接跳转
import Routes from './router-config.js' // 路由的配置文件
import iView from 'iview';
import 'iview/dist/styles/iview.css';

Vue.config.productionTip = false
Vue.use(VueRouter);
Vue.use(iView);

//如果在一个模块化工程中使用它，必须要通过 Vue.use() 明确地安装路由功能：
//创建路由
const router = new VueRouter({
  routes: Routes,
  //mode: "history"
})

Vue.directive('elsize',function(el,binding){
   let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
   //let clientWidth = document.documentElement.clientWidth || document.body.clientWidth;
   el.style.height=(clientHeight-binding.value.height)+"px"
   el.style.width = binding.value.width;
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router
})



